      invTime: 2000,
      isShow: false,
      curNum: 0,
      curCategory: 'eamv',
      subcategoryList:[],
      keyword:'',
      curInfo:{
        title:'',
        score:'',
        story:'',
        description:'',
        category:'',
        subcategory:'',
        imgurl:'',
        address:'',
        author: []
      },
      slides: [{ imgurl: require('../assets/noresource.jpg'), title: ''}],
      movieList: {
        "tv": {
          title: 'å‰§é›†',
          key:'tv',
          list: [
          { name: 'å›½äº§', id:'ch', url: '', hot: true},
          { name: 'æ¬§ç¾Ž', id:'ea', url: '', hot: true},
          { name: 'æ—¥éŸ©', id:'jk', url: ''}]
        },
        "mv": {
          title: 'ç”µå½±',
          key:'mv',
          last: true,
          list: [
          { name: 'å›½äº§', id:'ch', url: '', hot: true},
          { name: 'æ¬§ç¾Ž', id:'ea', url: '', hot: true},
          { name: 'æ—¥éŸ©', id:'jk', url: ''}]
