       }).catch(function (err) {
           console.log("error:" + err.message)
       })
    },
    FirstPage: function(){
      this.searchForList(this.curCategory, 0)
      this.curNum = 0
    },
    NextPage: function(){
      this.curNum += 20
      this.searchForList(this.curCategory, this.curNum)
    },
    LastPage: function(){
      if(this.curNum >= 20){
        this.curNum -= 20
        this.searchForList(this.curCategory, this.curNum)
      }
    }
  },
  computed:{
    showSlides:function(){
      if(this.slides.length <= 4 && this.slides.length>1){
        return this.slides
      }else {
        return this.slides.slice(0,4)
      }
    },
    restSlides:function(){
      if(this.slides.length <= 4){
        return
      }else{
        return this.slides.slice(4, this.slides.length)
      }
    }
  }
}
